    font-size: 100%;
    list-style: none;
}
.p-disabled {
    cursor: default !important;
}
.p-disabled a {
    cursor: default !important;
}
.p-component-overlay {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
}
.p-overflow-hidden {
    overflow: hidden;
}
.p-unselectable-text {
    -webkit-user-select: none;
    -moz-user-select: none;
    -o-user-select: none;
    -ms-user-select: none;
        user-select: none;
}
.p-scrollbar-measure {
    width: 100px;
    height: 100px;
    overflow: scroll;
    position: absolute;
    top: -9999px;
}
.p-clearfix:after {
    content: "";
    display: table;
    clear: both;
}

@-webkit-keyframes p-fadein {
  0%   { opacity: 0; }
  100% { opacity: 1; }
}
@keyframes p-fadein {
  0%   { opacity: 0; }
  100% { opacity: 1; }
}

input[type="button"],
input[type="submit"],
input[type="reset"],
input[type="file"]::-webkit-file-upload-button,
button {
    border-radius: 0;
}

/* Overlay Animations */
.p-input-overlay {
    transform: translateY(5%);
    opacity: 0;
    transition: transform .3s, opacity .3s;
}

.p-input-overlay-visible {
    transform: translateY(0);
    opacity: 1;
}

.p-input-overlay-hidden {
    opacity: 0;
    transform: translateY(5%);
    transition: transform .3s, opacity .15s;
}

.p-menu-overlay {
    transform: translateY(5%);
    opacity: 0;
    transition: transform .3s, opacity .3s;
}

.p-menu-overlay-visible {
    transform: translateY(0);
    opacity: 1;
}

.p-menu-overlay-hidden {
    opacity: 0;
    transform: translateY(5%);
    transition: transform .3s, opacity .15s;
}

.p-link {
	text-align: left;
	background-color: transparent;
	margin: 0;
	padding: 0;
	border: none;
	cursor: pointer;
}

/* Toggleable content with slide animation */
.p-toggleable-content-collapsed {
    display: none;
}

.p-toggleable-content-enter {
    max-height: 0;
    display: block;
}

.p-toggleable-content-enter-active {
    max-height: 1000px;
    overflow: hidden;
    transition: max-height cubic-bezier(0.86, 0, 0.07, 1) 400ms;
}

.p-toggleable-content-enter-done {
    display: block;
}

.p-toggleable-content-exit {
    max-height: 1000px;
    display: block;
}

.p-toggleable-content-exit-active {
    max-height: 0;
    display: block;
    overflow: hidden;
    transition: max-height cubic-bezier(0.86, 0, 0.07, 1) 250ms;
}

.p-toggleable-content-exit-done {
    display: none;
}

/* Floating Label */
.p-float-label {
    display: block;
    position:relative; 
}

.p-float-label > label {
  font-weight:normal;
  position:absolute;
  pointer-events:none;
  left: .25em;
  top: 50%;
  margin-top: -.5em;
  transition: 0.3s ease all; 
  -moz-transition: 0.3s ease all; 
  -webkit-transition: 0.3s ease all;
  color: #898989;
  line-height: 1;
}
.react-tabs {
  -webkit-tap-highlight-color: transparent;
}

.react-tabs__tab-list {
  border-bottom: 1px solid #aaa;
  margin: 0 0 10px;
  padding: 0;
}

.react-tabs__tab {
  display: inline-block;
  border: 1px solid transparent;
  border-bottom: none;
  bottom: -1px;
  position: relative;
  list-style: none;
  padding: 6px 12px;
  cursor: pointer;
}

.react-tabs__tab--selected {
  background: #fff;
  border-color: #aaa;
  color: black;
  border-radius: 5px 5px 0 0;
}

.react-tabs__tab--disabled {
  color: GrayText;
  cursor: default;
}

.react-tabs__tab:focus {
  box-shadow: 0 0 5px hsl(208, 99%, 50%);
  border-color: hsl(208, 99%, 50%);
  outline: none;
}

.react-tabs__tab:focus:after {
  content: "";
  position: absolute;
  height: 5px;
  left: -4px;
  right: -4px;
  bottom: -5px;
  background: #fff;
}

.react-tabs__tab-panel {
  display: none;
}

.react-tabs__tab-panel--selected {
  display: block;
}

